import { stopSubmit } from "redux-form";
import { profileAPI } from "../../src/api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_STATUS = 'SET-STATUS';
const GET_MY_PROFILE = 'GET-MY-PROFILE'
const DELETE_POST = 'DELETE-POST'
const SAVE_PHOTO_SUCCESS = 'SAVE-PHOTO-SUCCESS'
const SET_EDIT_MODE = 'SET-EDIT=MODE'

let initialState = {
    posts: [
        { id: 1, message: 'Hi i am here', likesCount: 15 },
        { id: 2, message: 'It\'s my first post', likesCount: 20 },
    ],
    profile: null,
    status: '',
}
const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let text = action.NewPostText;
            return {
                ...state,
                posts: [...state.posts, { id: 3, message: text, likesCount: 0 }]
            }
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }
        case SET_STATUS: {
            return {
                ...state, status: action.status
            }
        }
        case DELETE_POST: {
            return {
                ...state, posts: state.posts.filter(p => p.id != action.postId)
            }
        }
        case SAVE_PHOTO_SUCCESS: {
            return {
                ...state, profile: { ...state.profile, photos: action.photos }
            }
        }
        default:
            return state;
    }
};

export const addPost = (NewPostText) => ({ type: ADD_POST, NewPostText });
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({ type: SET_STATUS, status })
export const getMyProfile = (profile) => ({ type: GET_MY_PROFILE, profile });
export const deletePost = (postId) => ({ type: DELETE_POST, postId });
export const savePhotoSuccess = (photos) => ({ type: SAVE_PHOTO_SUCCESS, photos })

export const openUserProfile = (userId) => async (dispatch) => {
    let data = await profileAPI.getProfile(userId)
    dispatch(setUserProfile(data));
}
export const getStatus = (userId) => async (dispatch) => {
    let data = await profileAPI.getStatus(userId)
    dispatch(setStatus(data))
}
export const updateStatus = (status) => async (dispatch) => {
    let data = await profileAPI.updateStatus(status)
    if (data.resultCode === 0) {
        dispatch(setStatus(status))
    }
}
export const savePhoto = (file, id) => async (dispatch) => {
    debugger
    let data = await profileAPI.savePhoto(file)
    if (data.resultCode === 0) {
        dispatch(savePhotoSuccess(data.data.photos))
        openUserProfile(id)
    } 

}
export const saveProfile = (profile) => async (dispatch, getState) => {
    const userId = getState().auth.userId
    let data = await profileAPI.saveProfile(profile)
    if (data.resultCode === 0) {
      dispatch(openUserProfile(userId))
    } else {
        dispatch(stopSubmit('EditProfile', { _error: data.messages[0] }))
        return Promise.reject()
    }
}
export default profileReducer;