import { authAPI, profileAPI, securityAPI } from "../../src/api/api";
import { stopSubmit } from "redux-form"

const SET_USER_DATA = 'auth/SET-USER-DATA';
const SET_MY_USER_DATA = 'auth/SET-MY-USER-DATA'
const GET_CAPTCHA_URL_SUCCESS = 'auth/GET-CAPTCA-URL-SUCCESS'

let initialState = {
    userId: null,
    email: null,
    login: null,
    myProfile: null,
    isAuth: false,
    captchaUrl: null
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        case SET_MY_USER_DATA:
            return {
                ...state,
                myProfile: action.data,
            }
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state, captchaUrl: action.captchaUrl
            }
        default:
            return state;
    }
}
export const setAuthUserData = (userId, email, login, isAuth) => ({ type: SET_USER_DATA, payload: { userId, email, login, isAuth } });
export const setMyUserData = (data) => ({ type: SET_MY_USER_DATA, data })
export const getCaptchaUrlSuccess = (captchaUrl) => ({ type: GET_CAPTCHA_URL_SUCCESS, captchaUrl })

export const getAuthUserData = () => async (dispatch) => {
    let data = await authAPI.me();
    if (data.resultCode === 0) {
        let { id, email, login } = data.data;
        dispatch(setAuthUserData(id, email, login, true));
        profileAPI.getProfile(id).then(data => {
            dispatch(setMyUserData(data));
        })
    }
}
export const login = (email, password, rememberMe, captcha) => async (dispatch) => {
    let data = await authAPI.login(email, password, rememberMe, captcha)
    if (data.resultCode === 0) {
        dispatch(getAuthUserData());
        dispatch(getCaptchaUrlSuccess(null))
    } else {
        if (data.resultCode === 10) {
            debugger
            dispatch(getCaptchaUrl())
        }
            let message = data.messages.length > 0 ? data.messages[0] : 'Some error'
            dispatch(stopSubmit('Login', { _error: message }))
        
    }
}
export const getCaptchaUrl = () => async (dispatch) => {
    let data = await securityAPI.getCaptcha()
    const captchaURL = data.url
    dispatch(getCaptchaUrlSuccess(captchaURL))
}
export const logout = (email, password, rememberMe) => async (dispatch) => {
    let data = await authAPI.logout(email, password, rememberMe)
    if (data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
}

export default authReducer;