import { NavLink } from 'react-router-dom';
import s from '../Header.module.css';
import { SettingsIcon, ModeIcon, HelpIcon, LogOutIcon } from './MenuItemIcon';

const PhotoWithSettings = (props) => {
    return (
        <div className={s.divClickPhoto}>
            <div>
                <img src={!props.myProfile ? props.UserPhoto : props.myProfile.photos.small}></img>
                <span className={s.itemName}>{props.myProfile.fullName}
                </span>
            </div>
            <div className={s.item}>
                <SettingsIcon />
                <NavLink to='/settings'>Settings</NavLink>
            </div>
            <div className={s.item}>
                <ModeIcon />
                <span>Mode</span>
            </div>
            <div className={s.item}>
                <HelpIcon />
                <span>Help</span>
            </div>
            <div className={s.item} onClick={props.logout}>
                <LogOutIcon />
                <span>Log out</span>
            </div>
        </div>
    )
}
export default PhotoWithSettings;